html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*.after {
  box-sizing: inherit;
}

body {
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
}
body.body-fijo {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p,
span {
  font-size: 2rem;
  color: #0d0b04;
  line-height: 1.5;
}

.contenedor {
  width: 100%;
  max-width: 100rem;
  margin: 2rem auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0 0 1 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

img {
  width: 100%;
  height: auto;
  max-width: 100%;
  display: block;
}

label {
  font-weight: bold;
}

.body {
  background-color: linear-gradient(to top, #d5d8dc 0%, #fcfefe 100%);
}

.header {
  background-color: #e00029;
}
@media (min-width: 768px) {
  .header {
    box-shadow: 0.3rem 0.3rem 0.3rem #333;
    width: 100%;
    left: 0;
    top: 0;
  }
}
.header .contenido-header {
  padding: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
  }
}
.header h1 {
  color: #fcfefe;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
@media (min-width: 768px) {
  .header .navegacion-principal {
    display: flex;
  }
}
.header .navegacion-principal a {
  background-color: #e00029;
  display: block;
  font-weight: bold;
  color: #fcfefe;
  font-size: 2rem;
  padding: 1.5rem 1rem;
}
.header .navegacion-principal a:hover {
  color: #1a1a1a;
}
.header .navegacion-principal .navegacion-principal > li {
  float: left;
}
.header .navegacion-principal .navegacion-principal li,
.header .navegacion-principal ul {
  position: absolute;
  min-width: 140px;
  display: none;
}
.header .navegacion-principal .sub-menu {
  font-size: 1.5rem;
}
.header .navegacion-principal li:hover > ul {
  display: block;
}
.header .dark-mode-boton {
  max-width: 6rem;
}
.header .dark-mode-boton:hover {
  cursor: pointer;
}
.header ul,
.header li {
  list-style: none;
}

.contenedor {
  padding: 2rem;
}
.contenedor .logo_card {
  height: auto;
  width: 20rem;
}

.element {
  display: flex;
  align-items: center;
}

.grid {
  display: grid;
  grid-template-columns: repeat(4, auto);
  gap: 1rem;
}
.grid .card1 {
  grid-row: 1/3;
}
.grid .card2 {
  grid-column: 2/4;
  justify-self: center;
  text-align: center;
  margin-top: 1rem;
}
.grid .card3 {
  grid-row: 2/3;
  justify-self: center;
  text-align: center;
  margin-top: 1rem;
}
.grid .card4 {
  grid-row: 2/4;
  justify-self: center;
  text-align: center;
  margin-top: 1rem;
}
.grid .card5 {
  grid-column: 4/5;
  justify-self: center;
  text-align: center;
  margin-top: 1rem;
}
.grid .card6 {
  justify-self: center;
  text-align: center;
  margin-top: 1rem;
}
.grid .card3 .card4 .card5 .card6 {
  justify-self: center;
}
.grid .element {
  justify-self: center;
}
.grid .input-text {
  margin-top: 1rem;
  width: 100%;
  border: none;
  padding: 0.8rem;
  border-radius: 1rem;
  appearance: none;
  border: #0d0b04 0.1rem solid;
}

.sombra {
  box-shadow: 0px 2px 5px 1px #0a3232;
  /* caja */
  background-color: #fcfefe;
  border-radius: 0.2rem;
  /* borde sombra redondeado */
  padding: 2rem;
  /* padding para separación */
}

.titulos {
  display: flex;
  justify-content: space-around;
}

.formulario__campo {
  text-align-last: center;
  background-color: #d5d8dc;
  display: flex;
  width: 100%;
  border: none;
  border-radius: 4px;
  font-family: "Raleway", sans-serif;
  margin-top: 1rem;
  padding: 0.8rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}

main .text-area {
  resize: none;
  display: block;
  height: 12rem;
  width: 100%;
  margin-top: 1rem;
  padding: 0.8rem;
  border-radius: 1rem;
  border: #0d0b04 0.1rem solid;
}
main h2 {
  text-align: center;
  padding: 15px 0 15px 0;
}

.table-container {
  margin: 2rem auto;
}

.tablas {
  background-color: #fcfefe;
  width: 100%;
  text-align: left;
  border-collapse: collapse;
  border: 0.1rem solid #0d0b04;
  border-radius: 1rem;
}

th,
td {
  text-align: center;
  border: 0.1rem solid #0d0b04;
  padding: 1.5rem;
}

.sub-titulos {
  font-size: 2rem;
  text-align: center;
  background-color: #e00029;
  border-bottom: solid 0.5rem #0d0b04;
  color: #fcfefe;
}

tr:nth-child(even) {
  background-color: #d5d8dc;
}

tr:hover {
  background-color: #ff002f;
  color: #fcfefe;
}

.text {
  border: none;
  padding: 15px 20px;
  border-radius: 8px;
  border: transparent;
}

.flex-container {
  text-align: center;
  margin: 1rem;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.centrar-responsables {
  border: 0.1rem solid #ff002f;
  border-radius: 1rem;
  margin-top: 1rem;
  padding: 0.8rem;
  width: 100%;
}

.bt-file input[type=file] {
  appearance: none;
  display: none;
  visibility: hidden;
  opacity: 0;
}

.bt-file {
  display: inline-block;
  position: relative;
  background-color: #ff002f;
  color: #fcfefe;
  border: 0;
  padding: 15px 20px;
  border-radius: 8px;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.15);
  overflow: hidden;
  transition: ease-out 120ms background-color;
}

.bt-file:hover {
  background-color: #e00029;
}

.bt-file:focus {
  background-color: #e00029;
}

.bt-file > i {
  margin-right: 15px;
}

.bt-file label {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  top: 0;
  left: 0;
  cursor: pointer;
}

.bt-file.bordeado {
  background-color: transparent;
  border: 2px solid #ff002f;
  color: #e00029;
}

.bt-file.bt-file.bordeado:hover {
  background-color: #ff002f;
  color: #fcfefe;
}

.dark-mode {
  background-color: black;
}
.dark-mode .header {
  background-color: #e00029;
  box-shadow: 0.3rem 0.3rem 0.3rem #999;
}
.dark-mode h3,
.dark-mode h2,
.dark-mode h1 {
  color: #fcfefe;
}
.dark-mode .contenedor {
  color: #fcfefe;
  background-color: #1a1a1a;
}
.dark-mode th,
.dark-mode .text {
  color: #0d0b04;
}
.dark-mode td {
  color: #d5d8dc;
}
.dark-mode main .text-area {
  background-color: #333;
  color: #d5d8dc;
}
.dark-mode main .tablas {
  background-color: #333;
  color: #d5d8dc;
}
.dark-mode .input-text {
  background-color: #333;
  color: #d5d8dc;
}
.dark-mode .formulario__campo {
  background-color: #333;
  color: #d5d8dc;
}
.dark-mode .sub-titulos {
  background-color: #e00029;
  border-bottom: solid 0.5rem #d5d8dc;
}
.dark-mode tr:nth-child(even) {
  background-color: #0d0b04;
}
.dark-mode tr:hover {
  background-color: #ff002f;
  color: #d5d8dc;
}
.dark-mode td,
.dark-mode th {
  border: 0.1rem solid #9e9d9d;
  color: #fcfefe;
}
.dark-mode .dark-mode-boton,
.dark-mode .header_logo {
  filter: invert(100%);
}
.dark-mode input {
  background-color: #0d0b04;
}
.dark-mode .text {
  color: #fcfefe;
}
.dark-mode .bt-file.bordeado {
  background-color: transparent;
  border: 2px solid #ff002f;
  color: #e00029;
}
.dark-mode .bt-file.bt-file.bordeado:hover {
  color: #fcfefe;
}

.centrado {
  margin: 0;
  padding: 0;
  text-align: center;
}
.centrado .contenido-header {
  justify-content: center;
}
.centrado a {
  color: #0d0b04;
  font-size: 2rem;
  padding: 1.5rem 1rem;
}
.centrado a:hover {
  color: #ff002f;
}
.centrado .acceder input[type=text],
.centrado .acceder input[type=password] {
  padding: 20px;
  display: block;
  width: 300px;
  border: 1px solid #d5d8dc;
  margin: 20px auto;
}
.centrado .acceder .bt-file {
  width: 300px;
  margin: 20px auto;
  cursor: pointer;
}
.centrado .centrado-login {
  padding: 20px 0;
  margin-bottom: 10px;
  justify-content: center;
}
.centrado h1 {
  padding: 25px 0 25px;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #fcfefe;
  font-weight: bold;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: #ff002f;
}